import { createSlice } from '@reduxjs/toolkit';
// utils
import axios from '../../utils/axios';
import jwt_decode from 'jwt-decode'

// ----------------------------------------------------------------------

function objFromArray(array, key = 'id') {
  return array.reduce((accumulator, current) => {
    accumulator[current[key]] = current;
    return accumulator;
  }, {});
}

const initialState = {
  isLoading: false,
  error: false,
  mails: { byId: {}, allIds: [], count: 0 },
  labels: [],
  page: 1,
  perpage: 10,
  search: '',
  updateReply: false,
  toggleCompose: false,
};

const slice = createSlice({
  name: 'mail',
  initialState,
  reducers: {
    // START LOADING
    startLoading(state) {
      state.isLoading = true;
    },

    // HAS ERROR
    hasError(state, action) {
      state.isLoading = false;
      state.error = action.payload;
    },

    // GET LABELS
    getLabelsSuccess(state, action) {
      state.isLoading = false;
      state.labels = action.payload;
    },

    // GET MAILS
    getMailsSuccess(state, action) {
      console.log('results', action)

      const mails = action.payload;

      state.isLoading = false;
      state.mails.byId = objFromArray(mails);
      state.mails.allIds = Object.keys(state.mails.byId);
    },

    // GET MAIL
    getMailSuccess(state, action) {
      const mail = action.payload;

      state.mails.byId[mail.id] = mail;
      // if (!state.mails.allIds.includes(mail.id)) {
      //   state.mails.allIds.push(mail.id);
      // }
    },

    // SET PAGE
    getpage(state, action) {
      state.page = action.payload.page;
      state.perpage = action.payload.perpage;
    },

    // SET ALL MAIL COUNT
    setcount(state, action) {
      state.mails.count = action.payload;
    },

    // SET ALL MAIL COUNT
    setsearch1(state, action) {
      state.search = action.payload;
    },

    // updateReply
    updateReply(state, action) {
      state.updateReply = !state.updateReply
    },

    // toggleCompose
    toggleCompose(state, action) {
      state.toggleCompose = action.payload;
    },
  }
});

// Reducer
export default slice.reducer;

// ----------------------------------------------------------------------

export function getLabels() {
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.get('/api/mail/labels');
      console.log(response.data.labels)
      dispatch(slice.actions.getLabelsSuccess(response.data.labels));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function getMails(params, page, perpage, search) {
  var category = params.systemLabel;
  console.log(category)
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      const url = `${process.env.REACT_APP_SERVER_URL}/receiveEmail`;
      let data = {
        category: category,
        page: page,
        perpage: perpage,
        search: search,
        email: jwt_decode(localStorage.getItem('token')).email
      }
      let request = new Request(url, {
        method: 'POST',
        body: JSON.stringify(data),
        headers: new Headers({
          'Content-Type': 'application/json; charset=UTF-8'
        })
      });

      var results = [];
      await fetch(request)
        .then(response => response.json())
        .then(async data => {
          for (const i in data.data) {
            // console.log(data.data[i].message)
            var temp = {
              createdAt: data.data[i].createdAt,
              files: [],
              folder: "inbox",
              from: {
                avatar: "",
                email: data.data[i].fromEmail,
                name: data.data[i].fromName
              },
              id: data.data[i].id,
              isImportant: data.data[i].isImportant,
              isStarred: data.data[i].isStarred,
              isUnread: data.data[i].isUnread,
              labelIds: [],
              message: removeHtmlTags(data.data[i].message),
              subject: data.data[i].subject,
              accept: data.data[i].accept,
              reply: data.data[i].reply,
              to: {
                avatar: "",
                email: data.data[i].toEmail,
                name: data.data[i].toName
              },
            }
            results.push(temp)
          }
          dispatch(slice.actions.getMailsSuccess(results));
          setTimeout(() => {
            dispatch(slice.actions.getMailsSuccess(results));
          }, 2000);
          dispatch(slice.actions.setcount(data.count))
        })
        .catch(error => console.error(error));


    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}


