export function getMail(mailId) {
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      const url = `${process.env.REACT_APP_SERVER_URL}/detailEmail`;
      var data = {
        id: mailId
      }
      let request = new Request(url, {
        method: 'POST',
        body: JSON.stringify(data),
        headers: new Headers({
          'Content-Type': 'application/json; charset=UTF-8'
        })
      });

      fetch(request)
        .then(response => response.json())
        .then(data => {
          var temp = {
            createdAt: data.data[0].createdAt,
            files: [],
            folder: "inbox",
            from: {
              avatar: "",
              email: data.data[0].fromEmail,
              name: data.data[0].fromName
            },
            id: data.data[0].id,
            isImportant: data.data[0].isImportant,
            isStarred: data.data[0].isStarred,
            isUnread: data.data[0].isUnread,
            labelIds: [],
            message: (data.data[0].message),
            subject: data.data[0].subject,
            to: {
              avatar: "",
              email: data.data[0].toEmail,
              name: data.data[0].toName
            },
            accept: data.data[0].accept,
            inReplyTo: data.data[0].inReplyTo,
            messageId: data.data[0].messageId,
            mainId: data.data[0].mainId,
          }
          console.log(temp)
          dispatch(slice.actions.getMailSuccess(temp));
        })
        .catch(error => console.error(error));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

const removeHtmlTags = (html) => {
  const regex = /<[^>]+>/g;
  return html.replace(regex, '');
};

export function getpage(pageInfo) {
  return async (dispatch) => {
    dispatch(slice.actions.getpage(pageInfo));
  };
}

export function setsearch1(val) {
  return async (dispatch) => {
    dispatch(slice.actions.setsearch1(val));
  };
}

export function updateReply() {
  return async (dispatch) => {
    dispatch(slice.actions.updateReply());
  };
}

export function settoggleCompose(value) {
  return async (dispatch) => {
    console.log(value)
    dispatch(slice.actions.toggleCompose(value));
  };
}




